import React, { useState } from 'react'

export default function Text(props){
    const [value, setValue] = useState('')

    const onBlur = (e) => {
        const {name, value} = e.target
        const valorProcessado = value.trimLeft().trimRight().replace(/(\s{2,})/, ' ')
        const target = { name, value : valorProcessado }

        onChange({target})
        if(props.onBlur !== undefined) props.onBlur(e)
    }
    
    const onChange = (e) => {
        const {name, value} = e.target
        let valorProcessado = value
        
        if(props.allowedchars !== undefined && props.allowedchars !== ''){
            valorProcessado = valorProcessado.replace( new RegExp(`([^${props.allowedchars}])`), '')
        }
        else if(props.forbiddenhars !== undefined && props.forbiddenhars !== ''){
            valorProcessado = valorProcessado.replace( new RegExp(`([${props.forbiddenhars}])`), '')
        }

        if(props.uppercase !== undefined && props.uppercase !== false){
            valorProcessado = valorProcessado.toUpperCase()
        }
        else if(props.lowercase !== undefined && props.lowercase !== false){
            valorProcessado = valorProcessado.toLowerCase()
        }

        const target = { name, value : valorProcessado }

        setValue( valorProcessado )
        if(props.onChange !== undefined) props.onChange({target})
    }

    return <input {...props} type="text" value={value} onBlur={onBlur} onChange={onChange}/>
}