import {host, doRequest} from './Api'

const path = '/locais_de_estagio'

export const getBaseUrl = () => `${host}${path}`

export function ListaTodosAtestadosParaUmCurso(cursoId, onSuccess, onError){
    const request = {
        url : `${host}${path}/${cursoId}/curso_id`,
        onSuccess,
        onError
    }
    
    doRequest(request)
}