import * as Requerimentos from './Requerimentos'
import * as Concedentes from './Concedentes'
import * as CamposDeEstagio from './CamposDeEstagio'
import * as Cursos from './Cursos'

export function Api(){
    this.Requerimentos = Requerimentos
    this.Concedentes = Concedentes
    this.CamposDeEstagio = CamposDeEstagio
    this.Cursos = Cursos
}

export const host = 'https://api-estagil-gm.herokuapp.com'

export function doRequest({url='', params={}, onSuccess=(result)=>result, onError=(error)=>error}){
    const defaultRequestParams = {
        method : 'GET',
        headers : {
            "Content-Type": "application/json",
            "Accept": "application/json",
        },
        mode : 'cors',
    }

    const requestParams = { ...defaultRequestParams, ...params }

    fetch(`${url}`, requestParams)
        .then(res => {
            if(res.ok)
                res.json().then(resultado => onSuccess(resultado))
            else
                res.text().then(erros => onError(erros))
        })
        .catch(erros => {
            onError(erros)
        })
}