import React, { useState } from 'react'

export default function CEP(props){
    const [cep, setCep] = useState('')

    const comMascara = (valor) => {
        valor = semMascara(valor)
        const comprimento = valor.length
        
        if(comprimento > 5 && comprimento <= 8)
            return valor.replace(/^(\d{5})(\d{1,3})$/, "$1-$2")

        return valor
    }

    const semMascara = (valor) => valor.replace(/[^\d]/, '').replace('-', '')

    const onChange = (e) => {
        const {name, value} = e.target
        const novoValor = semMascara(value)
        const target = { name, value : novoValor }

        setCep( novoValor )
        if(props.onChange !== undefined) props.onChange({target})
    }

    return <input {...props} type="text" value={comMascara(cep)} onChange={onChange} minLength="9" maxLength="9" pattern="^(\d{5})-(\d{3})$"/>
}