import React from 'react'
import {Formulario} from '../../forms-base'
import {Aluno, LocalDoEstagio, Orientacao, PosEnvio} from './Telas'

export default class Requerimento extends React.Component {
    constructor(){
        super()
        this.state = {
            extras : {
                listaDeCursos : [],
                listaDeLocaisDeEstagio : [],
            },
        }

        this.adicionaExtra = this.adicionaExtra.bind(this)
        this.enviaDados = this.enviaDados.bind(this)
    }

    componentWillMount(){
        this.carregaCursos()
    }

    adicionaExtra(chave, valor){
        this.setState(prevState => (
            { extras : { ...prevState.extras, [chave] : valor } }
        ))
    }

    enviaDados(dados, envioComSucesso, envioComErros){
        const {api} = this.props
        
        // controle do tempo de envio
        this.adicionaExtra('enviando', true)
        setTimeout(()=> this.adicionaExtra('envioDemorado',  5),  5000 )
        setTimeout(()=> this.adicionaExtra('envioDemorado', 10), 10000 )
        setTimeout(()=> this.adicionaExtra('envioDemorado', 15), 15000 )
        
        const onSuccess = function(resposta){
            this.adicionaExtra('enviando', false)
            envioComSucesso(resposta)
        }.bind(this)

        const onError  = function(erros){
            this.adicionaExtra('enviando', false)
            envioComErros(erros)
        }.bind(this)

        api.Requerimentos.Novo(dados, onSuccess, onError)
    }

    carregaCursos() {
        const {api} = this.props

        const onSuccess = function(listaDeCursos){
            this.setState(prevState => (
                { extras : {...prevState.extras, listaDeCursos} }
            ))
        }.bind(this)

        const onError = function(erros){
            console.log(erros)
        }

        api.Cursos.ListaTodos( onSuccess, onError)
    }

    carregaLocaisDeEstagio(cursoId) {
        const {api} = this.props

        const onSuccess = function(listaDeLocaisDeEstagio){
            this.setState(prevState => (
                { extras : {
                    ...prevState.extras,
                    listaDeLocaisDeEstagio,
                    carregaLocaisDeEstagio : false,
                }}
            ))
        }.bind(this)

        const onError = function(erros){
            console.log(erros)
        }

        api.CamposDeEstagio.ListaTodosAtestadosParaUmCurso(cursoId, onSuccess, onError)
    }

    render() {
        const {api} = this.props

        const telas = [
            Aluno,
            LocalDoEstagio,
            Orientacao,
        ]
        const {extras} = this.state

        if(extras.carregaLocaisDeEstagio === true && extras.cursoSelecionado !== undefined){
            this.carregaLocaisDeEstagio(extras.cursoSelecionado)
        }

        return (
            <div> 
                <div className="bg-secondary">
                    <h3 className="my-0 px-2 text-light">Requerimento de Estágio</h3>
                </div>
                <div className="container">
                    <Formulario
                        telas={telas}
                        telaPostSubmit={PosEnvio}
                        extras={extras}
                        onSubmit={this.enviaDados} 
                        adicionaExtra={this.adicionaExtra}
                        api={api}
                    />
                </div>
            </div>
        )
    }
  }