import React from 'react'

export default class Formulario extends React.Component {
    constructor(){
        super()
        this.state = {
            tela : {
                atual : null,
                anterior : null,
                proxima : null,
            },
            dados:{},

            envio : {
                erros : undefined,
                resultado : undefined,
            },
        }

        this.telaAnterior = this.telaAnterior.bind(this)
        this.telaSeguinte = this.telaSeguinte.bind(this)

        this.salvaDados = this.salvaDados.bind(this)
        this.processaFormSubmit = this.processaFormSubmit.bind(this)

        this.envioComSucesso = this.envioComSucesso.bind(this)
        this.envioComErros = this.envioComErros.bind(this)
    }

    componentWillMount(){
        this.setState(prevState => {
            const tela = {
                ...prevState.tela,
                anterior : 0,
                atual : 0,
                proxima : 1,
            }

            return {tela}
        })
    }

    telaSeguinte(){
        const {telas} = this.props

        this.setState(prevState => {
            const {atual} = prevState.tela

            if(atual <= telas.length){
                const tela = {
                    ...prevState.tela,
                    anterior : atual,
                    atual : atual+1,
                    proxima : atual+2,
                }

                return {tela}
            }
        })
    }

    telaAnterior(){
        this.setState(prevState => {
            const {atual} = prevState.tela

            if(atual > 0){
                const tela = {
                    ...prevState.tela,
                    anterior : atual-2,
                    atual : atual-1,
                    proxima : atual,
                }

                return {tela}
            }
        })
    }

    _getTela(){
        const {telas, telaPreSubmit, telaPostSubmit} = this.props
        const {atual} = this.state.tela

        const TelaPreSubmit = telaPreSubmit !== undefined ? telaPreSubmit : TelaPreSubmitPadrao
        const TelaPostSubmit = telaPostSubmit !== undefined ? telaPostSubmit : TelaPostSubmitPadrao

        const qtdTelas = telas.length

        if(atual >= 0 && atual < qtdTelas){
            return telas[atual]
        }
        else if(atual === qtdTelas){
            return TelaPreSubmit
        }

        else if(atual > qtdTelas){
            return TelaPostSubmit
        }
        
        return telas[0]
    }

    salvaDados(elemento, dados){
        this.setState(prevState => (
            {
                dados : {
                   ...prevState.dados,
                   [elemento] : dados
                }
            }
        ))
    }

    envioComSucesso(resultado){
        const envio = {resultado, erros:undefined}
        this.setState({envio})
    }

    envioComErros(erros){
        const envio = {erros, resultado:undefined}
        this.setState({envio})
    }

    processaFormSubmit(e){
        e.preventDefault();

        const {atual} = this.state.tela
        const {telas} = this.props

        if(atual >= telas.length){
            // remove as informações sobre envios
            const envio = {resultado:undefined, erros:undefined}
            this.setState({envio})

            // executa o callback de submissão do form e passa os callbacks para sucesso e erros 
            this.props.onSubmit(this.state.dados, this.envioComSucesso, this.envioComErros)

            // avança para a tela postSubmit
            this.telaSeguinte()
        }
        else if(atual >= 0){
            this.telaSeguinte();
        }
    }

    render(){
        const Tela = this._getTela()
        const telaAtual = this.state.tela.atual
        const totalDeTelas = this.props.telas.length
        const {dados, envio} = this.state
        const {extras} = this.props
        
        return (
            <form onSubmit={this.processaFormSubmit}>
                <Tela salvaDados={this.salvaDados} dados={dados} envio={envio} {...this.props}/>

                {
                    (telaAtual <= 0 || extras.enviando === true ||  envio.resultado !== undefined) ? '' :
                    <input
                        type="button"
                        value="Voltar"
                        onClick={() => this.telaAnterior()}
                    />
                }
                {
                    (envio.resultado !== undefined || extras.enviando === true) ? '' :
                    <input
                        type="submit"
                        value={
                            telaAtual < totalDeTelas ? 
                                "Avançar"
                            :
                                envio.erros === undefined? 
                                    "Enviar"
                                : 
                                    "Enviar Novamente"
                        }
                    />
                }
            </form>
        )
    }
}

const TelaPreSubmitPadrao = () => (
    <div>
        <h1>Finalizando...</h1>
        <p>Basta clicar no botão enviar para finalizarmos</p>
    </div>
)

const TelaPostSubmitPadrao = (props) => {
    const {envio} = props
    const {resultado, erros} = envio

    const enviadoComSucesso = resultado !== undefined
    const erroNoEnvio = erros !== undefined

    if(enviadoComSucesso){
        return (
            <div>
                <h1>Informações enviadas :)</h1>
            </div>
        )
    }

    if(erroNoEnvio){
        return (
            <div>
                <h1>Ops! O envio das informações retornou erro</h1>
                <p>Que tal voltar e revisar antes de tentar novamente?</p>
            </div>
        )
    }

    return (
        <div>
            <h1>Enviando...</h1>
        </div>
    )
}