import React from 'react'

export default class BarraDeNavegacao extends React.Component {
    state = {
        responsive : false
    }
    
    toggleMenu(e){
        e.preventDefault()

        this.setState(prevState => {
            const responsive = !prevState.responsive
            return {responsive}
        })
    }

    render(){
        const {logo} = this.props
        const {responsive} = this.state
        const responsiveClassName = (responsive ? 'responsive': '')

        return (
            <div className={`topnav ${responsiveClassName}`} id="myTopnav">

                <a href=" #" style={{padding:"5px 8px"}}>
                    {logo === undefined ? '' : <img src={this.props.logo} height="32" alt="IF Baiano"/>}

                    <label className="badge badge-success" style={{padding:"5px 5px", margin:"4px 0px"}}>
                        Estágios GM
                    </label>
                </a>

                <Link href="#requerimento">Requerimento</Link>
                <Link href="#lista_locais">Lista de Locais Cadastrados</Link>
                <Link href="#proposta_concedente">Propor Local para Estágios</Link>

                <a href=" #" className="icon" onClick={this.toggleMenu.bind(this)}>Menu</a>
                
            </div>
        )
    }
}

function Link(props){
    const {href, children} = props
    const {hash} = window.location
    const className = href === hash ? 'active' : ''

    return(
        <a href={href} className={'btn btn-success '+className}>{children}</a>
    )
}