import React, { Component } from 'react';
import {Inicio, Requerimento, PropostaDeConcedente, ListaDeLocaisCadastrados} from './Paginas'
import {BarraDeNavegacao} from './Componentes'
import {Api} from './Api'

const rotas = {
    '' : Inicio,
    '#requerimento' : Requerimento,
    '#proposta_concedente' : PropostaDeConcedente,
    '#lista_locais' : ListaDeLocaisCadastrados,
}

export default class App extends Component {
    constructor(){
        super()
        this.api = new Api()
        this.state = {
            rota : ''
        }
    }

    componentWillMount(){
        this.atualizaRota()

        window.onhashchange = (e) => this.atualizaRota()
    }

    atualizaRota(){
        const {hash} = window.location
        const rota = Object.keys(rotas).includes(hash) ? hash : ''

        this.setState({rota})
    }

    render() {
        const {rota} = this.state
        const Conteudo = rotas[rota]

        return (
            <div className="container bg-white p-0" style={{minHeight:"100vh"}}>
                    <BarraDeNavegacao logo="favicon.ico"/>
                    <Conteudo api={this.api}/>
            </div>
        );
    }
}
