import React from 'react'
import Text from './Text'

const fullAlpha = 'A-ZÀ-Üa-zà-ü '

export default function NomeCompleto(props){
    const onChange = (e) => {
        if(props.onChange !== undefined) props.onChange(e)
    }
    
    return <Text {...props} onChange={onChange} allowedchars={fullAlpha} pattern={`^[${fullAlpha}][${fullAlpha} ]{1,}[${fullAlpha}]$`}/>
}