import React from 'react'
import {Endereco, RepresentanteLegal} from '.'
import {Input} from '../../../../Componentes'

export default class Aluno extends React.Component {
    constructor(props){
        super(props)

        this.state = {
            nome : '',
            matricula : '',
            cpf : '',
            data_nascimento : '',
            telefone : '',
            curso : {},
            endereco : {
                cep : '',
                logradouro : '',
                bairro : '',
                cidade : '',
                uf : '',
            },
            representante_legal : {
                nome : '',
                cpf : '',
                parentesco : '',
                telefone : '',
            },
        }

        this.nomeSocial = React.createRef()
        this.email = React.createRef()

        this.onChange = this.onChange.bind(this)
    }

    componentDidMount(){
        const {dados} = this.props

        if(dados !== undefined){
            const {aluno} = this.props.dados
        
            if(aluno !== undefined){
                this.setState(prevState => ({...prevState, ...aluno}) )
            }
        }
    }

    componentWillUnmount(){
        const {representante_legal} = this.state

        const aluno = Object.keys(this.state)
            // Filtra os campos a depender do preenchimento (não é validação)
            .filter(chave => {
                // Não adiciona os dados do responsavel legal se o nome não for definido e preenchido
                if(chave === 'representante_legal') {
                    if(representante_legal.nome === '' | representante_legal.nome === undefined){
                        return false
                    }
                    return true
                }
                // Para os demais campos, adiciona apenas os campos com valores preenchidos
                else if(this.state[chave] !== '' && this.state[chave] !== undefined){
                    return true
                }
                // Caso não atenda as regras anteriores, não adicionar
                else{
                    return false
                }
            })

            //Monta o objeto aluno a partir dos campos filtrados
            .reduce((aluno, atributo) => {
                aluno[atributo] = this.state[atributo]
                return aluno
            },{})

        // Salva no requerimento os dados do aluno
        this.props.salvaDados('aluno', aluno)
    }
    
    onChange(e) {
        const {name, value} = e.target
        this.setState({[name]: value})
    }

    maiorIdade(){
        const {data_nascimento} = this.state

        if(data_nascimento === undefined || data_nascimento === '') return true

        // instancia a data em um objeto Date
        const dataDeNascimento = new Date(data_nascimento)

        // extrai dia, mes e ano da data informada
        const dia = dataDeNascimento.getDate();
        const mes = dataDeNascimento.getMonth();
        const ano = dataDeNascimento.getFullYear();

        // compara a data informada + 18 anos (dia+1 por iniciar em 0) com a data atual e verifica se há um intervalo maior ou igual a 18 anos
        const maiorIdade = new Date(ano+18, mes, dia+1) <= new Date()

        // persiste o resultado da comparacao no state
        return maiorIdade
    }

    render() {
        const { 
            curso,
            data_nascimento,
            email,
            endereco,
            representante_legal,
        } = this.state

        const {adicionaExtra, extras} = this.props
        const {listaDeCursos} = extras

        return (
            <div>
                <h5 className="my-1 text-dark">Informe seus dados cadastrais</h5>

                <div className="form-row">
                    <div className="form-group col-lg-2 col-md-3 col-sm-12 col-12">
                        <label htmlFor="matricula">Nº de Matrícula</label>
                        <Input className="form-control" id="matricula" name="matricula" type="number" onChange={this.onChange} required placeholder="somente números" minLength="6" maxLength="12" pattern="^[0-9]{6,12}$" />
                    </div>
                
                    <div className="form-group col-lg-2 col-md-3 col-sm-12 col-12">
                        <label htmlFor="cpf">CPF</label>
                        <label className="badge badge-pill badge-info">somente números</label>
                        <Input className="form-control" id="cpf" name="cpf" type="cpf" onChange={this.onChange} required/>
                    </div>

                    <div className="form-group col-lg-8 col-md-6 col-sm-12 col-12">
                        <label htmlFor="curso">Curso</label>
                        <select 
                            className="custom-select"
                            value={curso.id}
                            onChange={ (e) => {
                                const id = e.target.value
                                this.setState({ curso : {id} })
                                adicionaExtra("cursoSelecionado", id)
                                adicionaExtra("carregaLocaisDeEstagio", true)
                            }}
                            required
                        >
                            {
                                (listaDeCursos === undefined || listaDeCursos.length === 0) ?
                                    <option key='' value=''>Carregando lista de cursos...</option>    
                                :   
                                    <option key='false' value=''>Qual o seu curso?</option>
                            }
                            {
                                listaDeCursos.map(curso => <option key={curso.id} value={curso.id}>{curso.nome}</option>)
                            }
                        </select>
                    </div>

                    <div className="form-group col-lg-12 col-md-12 col-sm-12 col-12">
                        <label htmlFor="nome">Nome Completo</label>
                        <Input className="form-control" id="nome" name="nome" type="nome-completo" uppercase="true" placeholder="Exatamente como consta em seus documentos" onChange={this.onChange} required minLength="7" maxLength="60" />
                    </div>
                </div>

                
                <div className="form-row">
                    <div className="form-group col-lg-3 col-md-3 col-sm-5 col-12">
                        <label htmlFor="data_nascimento">Data de Nascimento</label>
                        <input className="form-control" id="data_nascimento" name="data_nascimento" type="date" value={data_nascimento} onChange={this.onChange} required />
                    </div>

                    <div className="form-group col-lg-3 col-md-3 col-sm-7 col-12">
                        <label htmlFor="telefone">Telefone</label>
                        <label className="badge badge-pill badge-info">somente números</label>
                        <Input className="form-control" id="telefone" name="telefone" type="telefone" onChange={this.onChange} required />
                    </div>

                    <div className="form-group col-lg-6 col-md-6 col-sm-12 col-12">
                        <label htmlFor="email">Email</label>
                        <input className="form-control" id="email" name="email" type="email" defaultValue={email!==undefined ? email : ''} ref={this.email} onChange={this.onChange} minLength="6" maxLength="70" />
                    </div>        
                </div>

                <Endereco onChange={this.onChange} value={endereco}/>
                { !this.maiorIdade() ? <RepresentanteLegal onChange={this.onChange} value={representante_legal} /> : null }
            </div>
        );
    }
}