import React from 'react'

import Text from './Text'
import Number from './Number'
import CPF from './CPF'
import NomeCompleto from './NomeCompleto'
import Telefone from './Telefone'
import CEP from './CEP'

export function Input(props){
    const {type} = props

    if(type.toLowerCase() === "text")
        return <Text {...props}/>

    if(type.toLowerCase() === "number")
        return <Number {...props}/>

    if(type.toLowerCase() === "cpf")
        return <CPF {...props}/>

    if(type.toLowerCase() === "nome-completo")
        return <NomeCompleto {...props}/>

    if(type.toLowerCase() === "telefone")
        return <Telefone {...props}/>

    if(type.toLowerCase() === "cep")
        return <CEP {...props}/>

    return <input {...props}/>
}



