import React from 'react'
import {Orientador, PlanoDeAtividades, CoordenadorDoCurso} from '.'

export default class Orientacao extends React.Component {
    constructor(){
        super()

        this.state = {
            orientador : {
                nome : '',
                siape : '',
                formacao : '',
            },
            plano_atividades : {
                area : '',
                objetivos : '',
                atividades : '',
            },
            coordenador_curso : {
                nome : '',
                siape : '',
            }
        }

        this.onChange = this.onChange.bind(this)
    }
    
    componentDidMount(){
        const {dados} = this.props

        if(dados !== undefined){
            const {orientador, plano_atividades, coordenador_curso} = this.props.dados
        
            if(orientador !== undefined){
                this.setState({orientador})
            }

            if(plano_atividades !== undefined){
                this.setState({plano_atividades})
            }

            if(coordenador_curso !== undefined){
                this.setState({coordenador_curso})
            }
        }
    }

    componentWillUnmount(){
        this.props.salvaDados('orientador', this.state.orientador)
        this.props.salvaDados('plano_atividades', this.state.plano_atividades)
        this.props.salvaDados('coordenador_curso', this.state.coordenador_curso)
    }

    onChange(e){
        const {name, value} = e.target
        this.setState({[name] : value})
    }

    render(){
        const {orientador, plano_atividades, coordenador_curso} = this.state

        return(
            <div>
                <Orientador dados={orientador} onChange={this.onChange}/>
                <PlanoDeAtividades dados={plano_atividades} onChange={this.onChange}/>
                <CoordenadorDoCurso dados={coordenador_curso} onChange={this.onChange}/>
            </div>
        )
    }
}