import {host, doRequest} from './Api'

const path = '/concedentes'

export const getBaseUrl = () => `${host}${path}`

export function NovaProposta(dados, onSuccess, onError){
    const request = {
        url : `${host}${path}`,
        params : {
            method : 'POST',
            body : JSON.stringify(dados)
        },
        onSuccess,
        onError
    }
    
    doRequest(request)
}

export function ListaConcedentes(onSuccess, onError){
    const request = {
        url : `${host}${path}`,
        onSuccess,
        onError
    }
    
    doRequest(request)
}