import React from 'react'
import {Input} from '../../../../Componentes'

export function CoordenadorDoCurso(props) {
    const onChange = (e) => {
        const {name, value} = e.target

        const target = {
            name : "coordenador_curso",
            value : {
                ...props.dados,
                [name]: value
            }
        }

        if(props.onChange !== undefined) props.onChange({target})
    }

    return(
        <>
            <h5 className="my-1 text-dark">Quem é o Coordenador do Curso que você estuda?</h5>
            <div className="form-row">
                <div className="form-group col-lg-10 col-md-8 col-sm-12 col-12">
                    <label htmlFor="nome">Nome Completo</label>
                    <Input className="form-control" id="nome" name="nome" type="nome-completo" uppercase="true" onChange={onChange} required minLength="7" maxLength="60" />
                </div>

                <div className="form-group col-lg-2 col-md-4 col-sm-12 col-12">
                    <label htmlFor="siape">SIAPE</label>
                    <Input className="form-control" id="siape" name="siape" type="number" onChange={onChange} required minLength="6" maxLength="8" />
                </div>
            </div>
        </>
    )
}