import React from 'react'

export default class ListaDeLocaisCadastrados extends React.Component {
    constructor(){
        super()
    
        this.state = {
            cursoId : undefined,
            listaDeCursos : [],
            listaDeLocais : undefined,
        }

        this.carregaCursos = this.carregaCursos.bind(this)
        this.carregaListaDeLocais = this.carregaListaDeLocais.bind(this)
    }

    componentWillMount(){
        this.carregaCursos()
    }

    carregaCursos() {
        const onSuccess = function(listaDeCursos){
            this.setState({listaDeCursos})
        }.bind(this)
        const onError = function(erros){
            console.log(erros)
        }
        const {api} = this.props
        api.Cursos.ListaTodos( onSuccess, onError)
    }

    carregaListaDeLocais(cursoId){
        const onSuccess = function(listaDeLocais){
            this.setState({listaDeLocais})
        }.bind(this)
        
        const onError = function(erros){
            console.log(erros)
        }
        
        const {api} = this.props
        api.CamposDeEstagio.ListaTodosAtestadosParaUmCurso(cursoId, onSuccess, onError)
    }

    render(){
        const {cursoId, listaDeLocais, listaDeCursos} = this.state

        const listaDeLocaisCarregada = listaDeLocais !== undefined
        const listaDeLocaisVazia = listaDeLocaisCarregada && listaDeLocais.length === 0
        const listaDeLocaisComResultado = listaDeLocaisCarregada && listaDeLocais.length > 0

        const cursoSelecionado = cursoId === undefined ? undefined : listaDeCursos.filter(curso=> curso.id === cursoId)[0]
        const voltar = () => { this.setState({cursoId:undefined,listaDeLocais:undefined}) }

        return(
            <div>
                <div className="bg-secondary">
                    <h3 className="my-0 px-2 text-light">Lista de locais cadastrados para estágio</h3>
                </div>
                <div className="container">
                    { 
                    // Box para seleção do curso
                        cursoId === undefined ? 
                            <div className="form-group">
                                <h5 className="my-1 text-dark" htmlFor="curso">Passo 1: Selecione seu curso</h5>

                                <select 
                                    className="custom-select"
                                    value={cursoId}
                                    onChange={ (e) => {
                                        const cursoId = e.target.value
                                        this.setState({cursoId})
                                        this.carregaListaDeLocais(cursoId)
                                    }}
                                    required
                                >
                                    {
                                        (listaDeCursos === undefined || listaDeCursos.length === 0) ?
                                            <option key='' value=''>Aguarde, carregando lista de cursos...</option>    
                                        :   
                                            <option key='false' value=''>Por favor, selecione aqui o seu curso</option>
                                    }
                                    {
                                        listaDeCursos.map(curso => <option key={curso.id} value={curso.id}>{curso.nome}</option>)
                                    }
                                </select>
                            </div>
                        : 
                            <a className="badge badge-primary mr-1" href="#lista_locais" onClick={voltar} children="Voltar"/>
                    // Final - Box para seleçao do curso
                    }

                    {

                        (cursoId !== undefined && !listaDeLocaisCarregada) ? 
                            // Lista de locais ainda sem resultados
                            <div>
                                <p>Aguarde</p>
                            </div>
                        :
                            listaDeLocaisComResultado ?
                                // Lista de locais retornou com resultados
                                <TabelaDeLocais listaDeLocais={listaDeLocais} cursoSelecionado={cursoSelecionado} voltar={voltar} />
                            : 
                                listaDeLocaisVazia ? 
                                    // Lista de locais retornou sem resultados
                                    <NenhumLocalCadastrado cursoSelecionado={cursoSelecionado} voltar={voltar}/>
                                :
                                    ''
                    }
                    
                </div>
            </div>
        )
    }
}

function TabelaDeLocais(props) {
    const {listaDeLocais, cursoSelecionado} = props

    return (
        <div>
            <strong>Campos de estágio cadastrados para o curso {cursoSelecionado.nome}</strong>
            <table className="table table-striped table-hover">
                <thead>
                    <th>Nome</th>
                    <th>Cidade</th>
                </thead>
                <tbody>
                {
                    listaDeLocais.map(local => (
                        <tr>
                            <td>{local.nome}</td>
                            <td>{local.endereco.cidade}</td>
                        </tr>
                    ))
                }
                </tbody>
            </table>
        </div>
    )
}

function NenhumLocalCadastrado(props) {
    const {cursoSelecionado} = props
    return (
        <div>
            <strong>Não localizamos nenhum local de estágio cadastrado para o curso {cursoSelecionado.nome}</strong>
            <p>Gostaria de propor algum campo de estágio? Clique <a className="badge badge-info" href="#proposta_concedente">aqui</a> e envie os dados do local para o NURI.<br/>
            Não esqueça de colocar nas observações da proposta alguma informação para entrarmos em contato contigo e te informar sobre o resultado desta parceria ;)</p>
        </div>
    )
}