import React, { useState } from 'react'

export default function CPF(props){
    const [cpf, setCpf] = useState('')

    const comMascara = (valor) => {
        valor = semMascara(valor)
        const comprimento = valor.length

        if(comprimento > 9 && comprimento <= 11)
            return valor.replace(/^(\d{3})(\d{3})(\d{3})(\d)/, "$1.$2.$3-$4")

        else if(comprimento > 6)
            return valor.replace(/^(\d{3})(\d{3})(.)/, "$1.$2.$3")
        
        else if(comprimento > 3)
            return valor.replace(/^(\d{3})(.)/, "$1.$2")
        
        return valor
    }

    const semMascara = (valor) => valor.replace(/([^\d]{1,})/, '').replace('.', '').replace('.', '').replace('-', '')

    const onChange = (e) => {
        const {name, value} = e.target

        const novoValor = semMascara(value)
        setCpf( novoValor )
        
        const target = { name, value : novoValor }
        if(props.onChange !== undefined) props.onChange({target})
    }

    return <input {...props} type="text" value={comMascara(cpf)} onChange={onChange} minLength="14" maxLength="14" pattern="^[0-9]{3}.[0-9]{3}.[0-9]{3}-[0-9]{2}$"/>
}