import React, { useState } from 'react'

export default function Number(props){
    const [value, setValue] = useState('')
    
    const onChange = (e) => {
        const {name, value} = e.target
        const trimedValue = value.trim().replace(/([^\d]{1,})/, '')
        const target = { name, value : trimedValue }
        
        setValue( trimedValue )
        if(props.onChange !== undefined) props.onChange({target})
    }

    return <input {...props} type="text" value={value} onChange={onChange}/>
}