import React, { Component } from 'react'
import {Supervisor} from '.';

export default class LocalDoEstagio extends Component {

    constructor(props){
        super(props)
        this.state = {
            local_do_estagio : {},
            supervisor : {
                nome : '',
                cargo : '',
                formacao : '',
                telefone : '',
            }
        }
        this.onChange = this.onChange.bind(this)
        this.onSelectChange = this.onSelectChange.bind(this)
    }

    componentDidMount(){
        const {dados} = this.props

        if(dados !== undefined){
            const {local_do_estagio, supervisor} = this.props.dados
        
            if(local_do_estagio !== undefined){
                this.setState({local_do_estagio})
            }

            if(supervisor !== undefined){
                this.setState({supervisor})
            }
        }
    }

    componentWillUnmount(){
        this.props.salvaDados('local_do_estagio', this.state.local_do_estagio)
        this.props.salvaDados('supervisor', this.state.supervisor)
    }

    onChange(e) {
        const {name, value} = e.target
        this.setState({[name]: value})
    }

    onSelectChange(e){
        const {listaDeLocaisDeEstagio} = this.props.extras

        const id = e.target.value
        const local = listaDeLocaisDeEstagio.filter(local => local.id === id)

        if(local.length >= 1){
            const {id, nome} = local[0]
            this.setState({ local_do_estagio : {id, nome} })
        }
    }

   
    render() {
        const {supervisor, local_do_estagio} = this.state
        const {id} = local_do_estagio

        const {extras} = this.props
        const {listaDeLocaisDeEstagio} = extras

        return (
            <div>
                <h5 className="my-1 text-dark">Onde você vai estagiar?</h5>

                <select 
                    className="custom-select custom-select-lg"
                    value={id}
                    onChange={ this.onSelectChange }
                    required
                >
                    <option key='' value=''>Selecione o local onde você realizará o estágio</option>
                    {
                        listaDeLocaisDeEstagio.map(localDeEstagio => {
                            const {id, nome} = localDeEstagio
                            return(
                                <option key={id} value={id}>{nome}</option>
                            )
                        })
                    }

                </select>
                
                { id === undefined ? '' : <Supervisor dados={supervisor} onChange={this.onChange} /> }
            </div>
        );
    }
}