import React, { useState } from 'react'

export default function Telefone(props){
    const [telefone, setTelefone] = useState('')

    const comMascara = (valor) => {
        valor = semMascara(valor)
        const comprimento = valor.length

        if(comprimento === 11)
            return valor.replace(/^(\d{2})(\d{5})(\d{4})$/, "($1)$2-$3")

        else if(comprimento === 10)
            return valor.replace(/^(\d{2})(\d{4})(\d{4})$/, "($1)$2-$3")
        
        else if(comprimento === 9)
            return valor.replace(/^(\d{5})(\d{4})$/, "$1-$2")
        
        else if(comprimento > 4)
            return valor.replace(/^(\d{4})(\d{1,})$/, "$1-$2")

        return valor
    }

    const semMascara = (valor) => (
        valor.replace(/^[0]{1,}/, '').replace(/[^\d]/, '').replace('-', '').replace('(', '').replace(')', '')
    )
    

    const onChange = (e) => {
        const {name, value} = e.target
        const novoValor = semMascara(value)
        const target = { name, value : novoValor }

        setTelefone( novoValor )
        if(props.onChange !== undefined) props.onChange({target})
    }

    return <input {...props} type="text" value={comMascara(telefone)} onChange={onChange} minLength="9" maxLength="14" pattern="^(\(\d{2}\)|)(\d{4,5})-(\d{4})$"/>
}