import React from 'react'
import ReactMarkdown from 'react-markdown'

const source = 'https://raw.githubusercontent.com/estagios-gm/aluno/master/Inicio.md'

export default class Inicio extends React.Component {
    constructor(){
        super()

        this.state = {
            conteudo : ''
        }
    }

    componentWillMount(){
        this.carregaConteudo()
    }

    carregaConteudo(){
        fetch(source)
            .then(res => res.text())
            .then(conteudo => this.setState((state) => ({ ...state, conteudo })))
            .catch((err) => console.error(err));
    }
    
    render(){
        const {conteudo} = this.state

        return (
            <div className="container">
                <ReactMarkdown source={conteudo} />
            </div>
        )
    }

}