import React from 'react'

import {Input} from '../../../../Componentes'

export class RepresentanteLegal extends React.Component {

    constructor(){
        super()
        this.email = React.createRef()
        this.onChange = this.onChange.bind(this)
    }

    onChange(e){
        const {name, value} = e.target
        
        const target = {
            name : "representante_legal",
            value : {
                ...this.props.value,
                [name] : value
            }
        }

        this.props.onChange({target})
    }

    render(){
        const {email} = this.props.value
        const onChange = this.onChange

        return(
            <>
                <h5 className="my-1 text-dark">Dados cadastrais do seu Representante Legal</h5>
    
                <div className="form-row">
                    <div className="form-group col-lg-10 col-md-12 col-sm-12 col-12">
                        <label htmlFor="nome">Nome Completo</label>
                        <Input className="form-control" id="nome" name="nome" type="nome-completo" uppercase="true" placeholder="Informe o nome completo do seu representante legal, sem abreviações" onChange={onChange} required minLength="7" maxLength="60" />
                    </div>
    
                    <div className="form-group col-lg-2 col-md-3 col-sm-5 col-12">
                        <label htmlFor="cpf">CPF</label>
                        <label className="badge badge-pill badge-info">somente números</label>
                        <Input className="form-control" id="cpf" name="cpf" type="cpf" onChange={onChange} required />
                    </div>
    
                    <div className="form-group col-lg-3 col-md-9 col-sm-7 col-12">
                        <label htmlFor="parentesco">Parentesco</label>
                        <Input className="form-control" id="parentesco" name="parentesco" type="text" uppercase="true" placeholder="Pai, Mãe..." onChange={onChange} required maxLength="20" pattern={`^.{2,20}$`}/>
                    </div>
    
                    <div className="form-group col-lg-3 col-md-4 col-sm-12 col-12">
                        <label htmlFor="telefone">Telefone</label>
                        <label className="badge badge-pill badge-info">somente números</label>
                        <Input className="form-control" id="telefone" name="telefone" type="telefone" onChange={onChange} required />
                    </div>
    
                    <div className="form-group col-lg-6 col-md-8 col-sm-12 col-12">
                        <label htmlFor="email">Email</label>
                        <input className="form-control" id="email" name="email" type="email" defaultValue={email!==undefined ? email : ''} ref={this.email} onChange={this.onChange} minLength="6" maxLength="70"/>
                    </div>
                </div>
            </>
        )
    }
}